import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./app/App";
import registerServiceWorker from "./registerServiceWorker";
import { HashRouter as Router, Route, Link } from "react-router-dom";
import TestPage1 from "./Practice/TestPage1";
import Viewport from "./app/common/Viewport";
import NavButton from "./app/common/NavMenuButton";

const CompleteApp = () =>
  <Router>
    <div>
      <NavButton>
        <Link to="/">Home</Link>
      </NavButton>
      <NavButton>
        <Link to="/TestPage1">TestPage1</Link>
      </NavButton>
      <NavButton>
        <Link to="/Viewport">Viewport</Link>
      </NavButton>
      <Route exact path="/" component={App} />
      <Route path="/TestPage1" component={TestPage1} />
      <Route path="/Viewport" component={Viewport} />
    </div>
  </Router>;

ReactDOM.render(<CompleteApp />, document.getElementById("root"));
registerServiceWorker();



