import styled from "styled-components";

const Viewport = styled.div`
  border-radius: 3px;
  padding: 0.25em 1em;
  margin: 0 1em;
  background: gray;
  color: #83cafe;
  border: 2px solid palevioletred;
  width: 1024px;
  height: 768px;
`;

export default Viewport;



