import styled from "styled-components";

const NavMenuButton = styled.button`
  border-radius: 3px;
  padding: 0.25em 1em;
  margin: 0 1em;
  background: transparent;
  color: #83cafe;
  border: 2px solid #83cafe;

  &:hover {
    background: #83cafe;
    color: white;
  }

`;

export default NavMenuButton;


